import  React from "react"
import './NewsPost.scss'
import ReactMarkdown from 'react-markdown';
import {cn} from "@bem-react/classname";
const PostCn = cn('news-post');
export const NewsPost = ({id, title, content, published, loaded, source, url, urlToImage }) =>{
    return (<div key={id} className={PostCn('container')}>

        <a href={url} className={PostCn('title')}>
            {title.match(/BRSM/i) ? 'üí©': '' }{title.length >0 ? title : url}{title.match(/BRSM/i) ? 'üí©': '' }
        </a>

        <div >
            <ReactMarkdown className={PostCn('content')} source={content} linkTarget='_blank' disallowedTypes={['image']} />
        </div>
    </div>)
}
